
.mu-dialog.repair{max-width:none;border-radius:5px;background:#fff url("../assets/home/repair-bg.png") no-repeat 0 0/100% 100%;width:810px;height:456px
}
.mu-dialog.repair .mu-dialog-title{display:none
}
.mu-dialog.repair .mu-dialog-body{height:100%;padding:0
}
.ml10{margin-left:10px
}
.mb0{margin-bottom:0 !important
}
.onlyApply .repairBody{-webkit-justify-content:center
}
.onlyApply .schedule,.onlyApply .history{display:none
}
.onlyHistory.repairWarp .history{display:block
}
.onlyHistory .repairBody{display:none
}
.onlyHistory .schedule,.onlyHistory .apply{display:none
}
.repairWarp{-webkit-justify-content:center;display:-webkit-flex;height:100%
}
.repairWarp .history{display:none
}
.repairBody{position:relative;height:100%;width:100%;padding:50px;-webkit-justify-content:space-between;display:-webkit-flex
}
.schedule{width:258px;height:356px;overflow:auto;-webkit-overflow-scrolling:touch;font-size:12px;color:#fff;-webkit-font-smoothing:antialiased
}
.schedule .gary{color:#959a9c;font-size:10px
}
.schedule .tac{text-align:center
}
.schedule .fz12{font-size:12px
}
.schedule dt{font-size:18px;font-weight:bold;margin-bottom:25px
}
.schedule .cloud{background:url("../assets/home/cloud.png") no-repeat left 3px;text-indent:18px;line-height:18px;letter-spacing:1px;color:#959a9c
}
.schedule .list{margin-top:30px
}
.schedule .list li{width:42px;height:42px;border-radius:50%;background-color:#fff;float:left;margin-right:10px;overflow:hidden
}
.schedule .list li img{width:100%;height:100%
}
.schedule .mt30{margin-top:30px
}
.schedule .user{background:url("../assets/home/user1.png") no-repeat left 3px;padding-left:20px
}
.schedule .mobile{background:url("../assets/home/phone1.png") no-repeat left 3px;padding-left:20px;margin-top:12px
}
.schedule .date{color:#959a9c;margin-top:15px;font-size:10px;background:url("../assets/home/ok.png") no-repeat left 4px;float:right;padding-left:12px
}
.schedule .card{display:-webkit-flex
}
.schedule .card .position{-webkit-font-smoothing:auto;margin-bottom:2px;font-size:12px
}
.schedule .card p{font-size:10px
}
.schedule .card p i{width:12px;height:15px;display:inline-block;vertical-align:top;background:url("../assets/home/star1.png") no-repeat center 2px
}
.schedule .card p i:nth-child(1){margin-left:5px
}
.schedule .card p i.off{background:url("../assets/home/star2.png") no-repeat center 0.4px
}
.schedule .card .headImg{width:50px;height:50px;margin-right:5px;border-radius:50%
}
.schedule .card .mobile{padding-left:11px;background-size:11%;margin-left:12px;background-position:left 2px
}
.schedule .comment{line-height:30px
}
.schedule .comment label{margin-right:10px;font-size:10px
}
.schedule .comment b{background:url("../assets/home/star.png") no-repeat left center;display:inline-block;width:19px;height:29px;vertical-align:top
}
.apply{width:356px;height:356px;padding:22px 48px 15px 48px;background-color:#fff;border-radius:5px;display:-webkit-flex;-webkit-flex-direction:column;-webkit-justify-content:center;-webkit-align-items:center
}
.apply dt{color:#4f5362;font-size:23px;font-weight:bold;margin-bottom:10px
}
.apply dd{display:-webkit-flex;width:100%;height:auto;margin-bottom:7px
}
.apply dd ::-webkit-input-placeholder{color:#a8acb9
}
.apply dd .theme input{background:#eff0f4 url("../assets/home/email.png") no-repeat 10px center
}
.apply dd .man input{background:#eff0f4 url("../assets/home/User-Icon.png") no-repeat 10px center
}
.apply dd .phone input{background:#eff0f4 url("../assets/home/Telephone-Icon.png") no-repeat 10px center
}
.apply dd input[type='text'],.apply dd textarea{background-color:#eff0f4;border:0;border-radius:3px;height:24px;width:100%;padding-left:28px;font-size:10px
}
.apply dd input[type='file']{display:none
}
.apply dd label{display:block;width:100%;border:0 !important
}
.apply dd label input[type='text']{margin-left:0 !important;border-radius:20px;outline:none;-webkit-box-shadow:none
}
.apply dd label p{font-size:9px;letter-spacing:2px;padding-left:6px;color:#a8acb9;display:block
}
.apply dd.file label{min-width:70px;width:70px;height:30px;border-radius:3px 0 0 3px;background:url("../assets/home/camera1.png") no-repeat 10px center
}
.apply dd ul{width:100%;height:30px;border-radius:0 3px 3px 0
}
.apply dd ul li{float:left;width:30px;height:100%;margin-right:10px
}
.apply dd textarea{resize:none;height:70px;padding:8px
}
.apply dd.last{height:25px;line-height:25px;-webkit-justify-content:center
}
.apply dd.last button{width:160px;color:#fff;font-size:12px;background-color:#00d5c3;border:0;border-radius:20px
}
.apply dd.last button:active{background-color:#1db9cc
}
.apply dd .status{font-size:10px;letter-spacing:2px;padding-left:20px;background:url("../assets/home/success.png") no-repeat left center
}
.history{color:#fff;width:100%;height:100%;display:-webkit-flex;-webkit-flex-direction:column
}
.history h1{font-size:34px;letter-spacing:4px;font-weight:bold
}
.history .t{height:180px;min-height:180px;-webkit-justify-content:center;-webkit-align-items:center;-webkit-flex-direction:column;display:-webkit-flex
}
.history .t p{font-size:9px;letter-spacing:4px;margin-bottom:25px
}
.history .t input{background-color:#fff;vertical-align:top;border-radius:20px 0px 0px 20px;border:0;width:315px;height:25px;padding-left:14px;font-size:10px
}
.history .t button{border-radius:0px 20px 20px 0px;border:0;height:25px;width:40px;vertical-align:top;background:#00d5c3 url("../assets/home/Search-Icon1.png") no-repeat center center
}
.history .b{width:100%;background-color:#fff;height:276px;color:#333;padding:0 43px;overflow:auto;-webkit-overflow-scrolling:touch;-webkit-flex-grow:2
}
.history .b li{-webkit-flex-direction:column;position:relative;padding-left:96px;-webkit-justify-content:center;height:112px;border-bottom:1px solid #f1f1f1;display:-webkit-flex;background:url("../assets/home/Avatar.png") no-repeat 13px center
}
.history .b li h2{font-size:14px;color:#000;margin-bottom:10px
}
.history .b li h2 span{color:#00d5c3;font-size:12px;margin-left:20px
}
.history .b li h2 .f_r{color:#ccc;line-height:21px
}
.history .b li p{font-size:11px;color:#aaa;line-height:20px
}
.history .b li button{position:absolute;color:#535861;border:1px solid #ccc;font-size:9px;background-color:#fff;border-radius:20px;width:55px;height:20px;right:0px;bottom:20px;-webkit-user-select:none
}
.history .b li button:active{background-color:#00d5c3;color:#fff;border-color:#fff
}



