@-webkit-keyframes fadeInAni {
  0% {
    opacity: 0;visibility: visible;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeOutAni {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
@-webkit-keyframes circleFadeInAni {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px)
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
    visibility: visible;
  }
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@-webkit-keyframes blur {
  0% {
    -webkit-filter: blur(5px);
  }

  100% {
    -webkit-filter: blur(0px);
  }
}

@-webkit-keyframes InfoFadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px)
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
    visibility: visible;
  }
}
@-webkit-keyframes fadeInOut {
  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
  }
  20% {
    opacity: 1;
    -webkit-transform: translateY(0px);
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    display: none;
    visibility: hidden;
  }
}
@-webkit-keyframes footerHide {
  0%{width:100%;height:120px;}
  100%{width:115px;height:50px;}
}



