
.top-icon{height:100%;width:224px;margin:0 15px;display:-webkit-flex;-webkit-align-items:center;-webkit-justify-content:center
}
.top-icon>div{display:block;width:40px;margin:0 2px;position:relative;background-image:url(../assets/home/spirit.png)
}
.top-icon>div .content{visibility:hidden;opacity:0;transition:all .5s ease;-webkit-transition:all .5s ease;position:absolute;left:50%;transform:translate(-50%, 20px);-webkit-transform:translate(-50%, 20px);top:36px
}
.top-icon>div:hover .content{animation:fadeInAni 1.3s ease forwards;visibility:visible;transform:translate(-50%, 0px);-webkit-transform:translate(-50%, 0px)
}
.top-icon>div i{display:block;width:18px;height:18px;background:#dc6263;color:white;border-radius:50% 50%;position:absolute;font-style:normal;text-align:center;line-height:18px;top:-7px;right:0px
}
.top-icon>div:nth-child(1){height:23px;background-position:1px 0px
}
.top-icon>div:nth-child(1):hover{background-position:-1px -64px
}
.top-icon>div:nth-child(2){height:17px;background-position:-39px 0px
}
.top-icon>div:nth-child(2):hover{background-position:-39px -68px
}
.top-icon>div:nth-child(3){height:18px;background-position:1px -31px
}
.top-icon>div:nth-child(3):hover{background-position:1px -103px
}
.top-icon>div:nth-child(4){height:18px;background-position:-39px -30px
}
.top-icon>div:nth-child(4):hover{background-position:-39px -102px
}
.top-icon>div:nth-child(5){height:18px;background-position:-79px -31px
}
.top-icon>div:nth-child(5):hover{background-position:-79px -67px
}
.message{position:relative;z-index:99
}
.message:hover .content>img{-webkit-transform:translateX(0)
}
.message .content{transform:translateX(335px) !important;-webkit-transform:translateX(335px) !important
}
.message .content .more:hover img{-webkit-transform:translateX(0)
}
.message .content .more .ac{position:absolute;height:50px;border-radius:50%;top:12px;width:50px;right:10px
}
.message .content img{width:273px;-webkit-transform:translateX(120%);transition:all .5s ease;-webkit-transition:all .5s ease
}
.video img{width:204px;position:relative;z-index:3;pointer-events:none
}
.video .content .addressBook{position:absolute;right:5px;width:45px;height:45px;bottom:44px
}
.video .content .addressBook:hover img{animation:fadeInAni 1.3s ease forwards;visibility:visible;transform:translateY(0px);-webkit-transform:translateY(0px)
}
.video .content .addressBook img{position:absolute;transform:translateY(20px);-webkit-transform:translateY(20px);visibility:hidden;opacity:0;transition:all .5s ease;-webkit-transition:all .5s ease;left:-154px;top:46px;z-index:2
}



